import {createReducer} from 'redux-decorated'
import {actions} from '../actions'

export const user = createReducer({})
  .when(actions.signIn, (state, {user, company, token}) =>
    ({user, company, token, fail: false, ...state}))
  .when(actions.signInFail, state => ({fail: true, ...state}))
  .when(actions.signOut, () => ({}))
  .when(actions.updateUser, (state, {loading, company, user}) => ({
    ...state,
    loading,
    company: company || state.company,
    user: user ? {...state.user, ...user} : state.user,
  }))
